.github-preview-card {
  width: 80%;
    padding: 15px;
  margin: auto;
    border-radius: 8px;
    max-width: 80%;
}
  
.github-preview-card h4 {
  font-size: 1.5em;
  margin-bottom: 12px;
  word-wrap: break-word;
  align-self: baseline; 
  overflow: hidden;
  text-align: left;
}

.github-preview-card h4 strong {
  white-space: nowrap;
  overflow:hidden;
  text-overflow: ellipsis;
  display: inline-block;
  vertical-align: text-bottom;
  max-width: 80%;
}

@media (max-width: 768px) {
  .github-preview-card h4:not(:has(strong)) {
    display: none;
  }
}
  
.github-preview-card p {
  margin-bottom: 10px;
}
  
.github-preview-card a {
  color: #0366d6;
  text-decoration: none;
  font-weight: bold;
}
  
.github-preview-card a:hover {
  text-decoration: underline;
}
  
.owner-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-right: 15px;
}
  
.repo-header {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  max-width: 80%;
}
  
.commit-info {
  margin-top: 20px;
}
  
.commit-info h3 {
  font-size: 1.2em;
  margin-bottom: 10px;
}
  
.commit-info ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
  
.commit-info li {
  margin-bottom: 8px;
  font-size: 0.9em;
}
  

@media (max-width: 768px) {
  .repo-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .owner-avatar {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
  