.members-preview {
    justify-content: flex-start !important;
    border-radius: 1em;
}

.tiny-member-card {
    border-radius: 1em;
    padding: 1em;
}

.tiny-member-card .header-section {
    gap: 0.5em;
}

.tiny-member-card img {
    max-width: 100px;
    max-height: 100px;
}

@media only screen and (max-width: 800px) {
    .tiny-member-card {
        margin: auto;
    }
}