/* Edit.css */

.member-container {
    margin-bottom: 20px;
    padding: 15px;
    border-radius: 1em;
    width: 80%;
    margin: auto;
}

form {
    display: flex;
    flex-direction: column;
}

label {
    margin-bottom: 10px;
}

input,
textarea {
    margin-bottom: 10px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.textareas {
    display: flex;
    justify-content: center;
    gap: 2em;
}

textarea {
    flex: 1;
    width: 100%;
    /* Allow textareas to expand and fill available space */
}

#edit-submit {
    background-color: #4caf50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

#edit-submit:hover {
    background-color: #45a049;
    transform: scale(1.01);
}