header {
    position: sticky;
    top: 0;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    background-color: rgba(0, 0, 0, 0.1);
    z-Index: 5;
}

.header-container {
    margin: auto;
    max-width: 1500px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.header-left {
    display: flex;
    gap: 2em;
    justify-content: space-between;
    align-items: center;
}

.menu-toggle {
    display: none;
}

@media only screen and (max-width: 1000px) {
    .logo {
        display: none;
    }
}

nav {
    display: flex;
    gap: 2em;
    justify-content: space-between;
}

nav a {
    color: inherit;
    transition: 0.3s;
}

.buttons button {
    background: none;
    border: none;
    border-radius: 1em;
    transition: 0.3s;
}

.buttons button:hover {
    background-color: rgb(32, 77, 255);
    box-shadow: 0 0 1em rgb(32, 77, 255);
}

.active {
    /*color: rgb(32, 77, 255);*/
    box-shadow: 0 0 4em rgb(32, 77, 255);
    /*text-shadow: 2px 2px 10px rgba(32, 77, 255);*/
    font-weight: bold;
    background-color: rgb(32, 77, 255);
    border-radius: 0.2em;
}

@media only screen and (max-width: 470px) {
    .menu-toggle {
        display: block;
    }
    .nav {
        display: none;
    }
    .open {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        background-color: rgba(0, 0, 0, 0.3);
        padding: 20px;
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 10;
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        border-radius: 0em 0em 0.5em 0.5em;
    }
}