::-webkit-scrollbar {
    width: 1em;
    max-width: 5%;
}

::-webkit-scrollbar-thumb {
    width: 0.5em;
    max-width: 5%;
    border-radius: 0.2em;
}

::-webkit-scrollbar-thumb:hover {
    -webkit-backdrop-filter: blur(2px);
}

::-webkit-scrollbar-track {
    -webkit-backdrop-filter: blur(20px);
}

::-webkit-scrollbar-track:hover {
    -webkit-backdrop-filter: blur(20px);
}

::-webkit-scrollbar-thumb:active {
    -webkit-backdrop-filter: blur(20px);
}