.project-preview-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1em;
    max-width: 100%;
}

.project-card {
    display: flex;
    flex-direction: column;
    border-radius: 1em;
    padding: 1em;
    justify-content: center;
    max-width: 100%;
    width: 90%;
}

.project-links {
    display: flex;
    padding: 1em;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: row;
    @media screen and (max-width: 1000px) {
        flex-direction: column;
    }
}

.project-title {
    font-size: 1.5em;
    margin-bottom: 10px;
}

.project-description {
    margin-bottom: 15px;
    border-radius: 0.5em;
}

.project-section {
    align-self: center;
    margin-top: 15px;
}

.project-section h3 {
    text-align: top;
}

.project-section iframe {
    max-width: 100%;
    border-radius: 0.5em;
}

.project-section a {
    color: #0366d6;
}

.no-projects-message {
    text-align: center;
    margin: 20px;
    font-style: italic;
    color: #888;
}

@media screen and (max-width: 1000px) {
    .project-section h3 {
        text-align: center;
    }
}