@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300');
body {
    font-family: "Source Code Pro", monospace;
    font-weight: 300;
    font-size: 14pt;
    margin: 0;
    padding: 0;
    transition: 0.5ms;
    margin: 0;
    z-index: 1;
}

.App {
    height: 100%;
    transition: 0.5ms;
}

.light {
    background-color: #fff;
    color: #333;
}

.dark {
    background: #0f0f23;
    color: #cccccc;
}

.section-title {
    font-size: 24px;
    margin-bottom: 16px;
}

.container {
    z-index: 1;
    max-width: 1400px;
    padding: 20px;
    border-radius: 8px;
    margin: 20px auto;
    backdrop-filter: blur(20px);
}

.dark .box {
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

.light .box {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}