.login-container {
    z-index: 1;
    padding: 20px;
    border-radius: 8px;
    margin: 20px auto;
    width: 40%;
    align-content: center;
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
}

.login-container h2 {
    text-align: center;
    color: #333;
}

.login-container form {
    margin: 2px auto;
    display: flex;
    flex-direction: column;
}

.login-container label {
    margin-bottom: 8px;
}

.login-container input {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
}

.login-container button {
    padding: 10px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.login-container button:hover {
    transform: scale(1);
    background-color: #45a049;
}