:root {
    --box-shadow-color: rgba(0, 0, 0, 0.9);
    /* Default box shadow color */
}

.member {
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 8px;
}

.header-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.header-section h2 {
    margin: 0;
}

.header-section img {
    border-radius: 50%;
    width: 200px;
    height: 200px;
    object-fit: cover;
    transition: 0.5s;
    /*box-shadow: 0 0 10px var(--box-shadow-color);*/
}

.about-section,
.skills-section {
    margin-top: 10px;
}

.list {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    justify-content: center;
}

.card {
    border-radius: 8px;
    padding: 16px;
    width: 40%;
    overflow: hidden;
    white-space: wrap;
    text-overflow: ellipsis;
    max-height: 100px;
    transition: 0.5s;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.card.last-even {
    width: calc(80% + 36px);
}

.header-section img:hover {
    transform: scale(1.3);
    border-radius: 5px;
    box-shadow: 0 0 4em rgb(32, 77, 255);
}

#expandbtn {
    z-index: 5;
    align-self: center;
    align-content: center;
    justify-self: center;
    text-align: center;
    padding: 8px 16px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.5s;
}

#expandbtn:hover {
    transform: scale(1.05);
}

.member-card {
    transition: 0.5s;
    max-width: 40%;
}

@media only screen and (max-width: 800px) {
    .header-section {
        flex-direction: column-reverse;
    }
    .member-card {
        max-width: 80%;
    }
    .card,
    .card.last-even {
        width: 80%;
    }
}