.hide-overflow {
    color: black;
    display: inline-block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.contact-card {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    list-style: none;
    gap: 1rem;
}

.contact-card li {
    border: 1px solid #ccc;
    padding: 1rem;
    border-radius: 5px;
}

.contact-card .social-icon {
    height: 2em;
    align-self: flex-start;
    transition: 0.5s;
}

.social-icon:hover {
    transform: scale(1.2);
    box-shadow: 0 0 2em rgb(32, 77, 255);
    border-radius: 2em;
}